<template>
  <div style="display: none">
    <slot></slot>
  </div>
</template>

<script>
import Mapbox from 'mapbox-gl'

export default {
  name: 'MarkerDefault',
  // render: () => null,
  data: () => ({
    initial: true,
    marker: undefined
  }),
  props: {
    lngLat: {
      type: Array,
      required: true
    },
    anchor: {
      type: String,
      default: 'center'
    }
  },
  mounted () {
    this.mapbox = Mapbox
    this.map = this.$store.map

    this.initial = false
    let element
    if (this.$slots.default) element = this.$slots.default[0].elm
    this.marker = new this.mapbox.Marker({
      element,
      anchor: this.anchor
    })
    this.marker.setLngLat(this.lngLat).addTo(this.map)
  },
  beforeDestroy () {
    if (this.map !== undefined && this.marker !== undefined) {
      this.marker.remove()
    }
  },
  watch: {
    lngLat: function (value) {
      if (this.initial) return
      this.marker.setLngLat(value)
    }
  }
}
</script>
