<template>
  <v-dialog
    v-model="value"
    hide-overlay
    persistent
    width="300"
  >
    <v-card>
      <v-card-title class="headline red lighten-2">
        Ошибка!
      </v-card-title>

      <v-card-text>
        <br>
        Произошла ошибка при загрузке данных. Свяжитесь с администратором системы.
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="primary"
          text
          @click="closeDialog()"
        >
          Закрыть
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'DialogError',
  data: () => ({
  }),
  props: {
    value: {
      type: Boolean,
      required: true
    }
  },
  methods: {
    closeDialog () {
      this.$emit('input', false)
    }
  }
}
</script>

